.popup-effect {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.7);
  -webkit-transition: opacity 0ms;
  transition: opacity 0ms;
  visibility: hidden;
  opacity: 0;
  z-index: 99;
}
.popup-effect:target {
  visibility: visible;
  opacity: 1;
}
.popup {
  background: #fff;
  border-radius: 4px;
  max-width: 500px;
  position: relative;
  margin: 8em auto;
  padding: 3em 2em;
  z-index: 999;
  text-align: center;
}
.popup .close {
  position: absolute;
  top: 5px;
  right: 15px;
  -webkit-transition: all 200ms;
  transition: all 200ms;
  font-size: 30px;
  font-weight: bold;
  text-decoration: none;
  color: #000;
}
