  nav ul li:hover > ul {
    display: none;
}
  /* Fisrt Tier Dropdown */
nav ul ul li {
    display: block;
    width: 100%;
    padding: 0;
}
nav ul ul ul li {
    position: static;
    /* has to be the same number as the "width" of "nav ul ul li" */
}
nav a {
    font-size: 15px;
}
