  background-size: cover;
  background-attachment: fixed;
  background-repeat: no-repeat;
}
::-moz-selection {
  background: #000;
  color: #fff;
}
::selection {
  background: #000;
  color: #fff;
}
h1, h2, h3, h4, h5,
