  color: #000;
}
a {
  -webkit-transition: .3s all ease;
  transition: .3s all ease;
}
a:hover {
  text-decoration: none;
}
.font-secondary, h1, h2, h3, h4, h5,
.h1, .h2, .h3, .h4, .h5 {
