}
.text-black {
  color: #000 !important;
}
.bg-black {
  background: #000 !important;
}
.line-height-sm {
  line-height: 1.3;
}
.line-height-xs {
  line-height: 1;
}
.form-control {
  border-radius: 0px;
}
.btn {
  border-radius: 0px;
}
